@import '../../variables.scss';

@mixin shared-height {
    height: 200px;
}

.country-details {
    display: flex;
    color: inherit;
    justify-content: space-between;
    transition: color 0.3s ease;

    @include for-mobile;

    @media (max-width: $medium-break) {
        flex-direction: column;
    }

    .country-img {
        flex: 0 1 45%;
        height: inherit;

        img {
            width: 100%;
            height: 100%;
            object-fit: fill;
        }
    }

    .details-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex: 0 1 50%;
        padding: 25px 0;
        padding-right: 30px;
        height: inherit;

        @media (max-width: $medium-break) {
            flex-direction: column;
            padding: 25px 0;
        }

        header {
            font-weight: 800;
            font-size: 30px;
            margin-bottom: 20px;
        }

        .details {
            columns: 2;
            height: 45%;
            height: 150px;
            column-fill: auto;

            @media (max-width: $medium-break) {
                columns: initial;
                height: auto;
            }

            .country-detail {
                margin: 11px auto;

                &:first-of-type {
                    margin-top: 0;
                }

                .detail-title {
                    font-weight: 600;
                }

                .detail-description {
                    margin-left: 3px;
                }

                @media (max-width: $medium-break) {
                    &:nth-of-type(6) {
                        margin-top: 30px;
                    }
                }
            }
        }

        .country-tags {
            display: flex;

            @media (max-width: $medium-break) {
                margin-top: 20px;
            }

            .detail-title {
                font-weight: 600;
                flex: 0 1 20ch;
                margin-top: 7px;
            }

            .tag-container {
                display: inline-block;

                .tag {
                    text-decoration: none;
                    color: inherit;
                    display: inline-block;
                    padding: 3px 15px;
                    text-align: center;
                    border: 2px solid rgba(0, 0, 0, 0.05);
                    margin: 0 5px;
                    margin-bottom: 5px;
                    min-width: 100px;
                    @include box-shadow;
                    background: var(--element-bg);
                    @include themeTransition;
                }
            }
        }
    }
}
